<template>
  <div id="app">
    <p class="weather-title">成都{{date | GetNowDateTime}}天气情况</p>
    <div class="weather">
      <img :src="weatherObj.day_weather_pic" alt="">
      <ul>
        <li>气温：{{weatherObj.day_air_temperature}}°C</li>
        <li>天气情况：{{weatherObj.day_weather}}</li>
        <li>日出日落时间：{{weatherObj.sun_begin_end}}</li>
        <li>降水概率：{{weatherObj.jiangshui}}</li>
      </ul>
    </div>
  </div>
</template>

<script>

  export default {
    data(){
      return {
        date: new Date(),
        weatherObj: {}
      }
    },
    filters: {
      GetNowDateTime: val => {
        let nowTime = val;
        let NowDateTime = nowTime.getFullYear() + "年" +
            (nowTime.getMonth() + 1 ) + "月" +
            nowTime.getDate() + "日" +
            " ";

        return NowDateTime;
      }
    },
    mounted: function () {
      let url = 'https://route.showapi.com/9-2';
      let showapi_appid = 'showapi_appid=32114';
      let showapi_sign = 'showapi_sign=6b8a7f08662149a98783bd2d401e984a';
      let city = 'area=成都'
      this.$http.get(url + '?' + showapi_appid + '&' + showapi_sign + '&' + city)
          .then(res => {
            this.weatherObj = res.body.showapi_res_body.f1;
          });
    },
    methods: {},
    components: {}
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
    font: 12px/24px "微软雅黑";
  }

  ul, li {
    list-style: none;
  }

  #app {
    padding: 20px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .weather-title{font-size: 24px;color: #1c8de0;}

  .weather {
    padding-top: 40px;
    display: flex;
  }

  .weather img {
    padding-right: 20px;
  }

</style>



